'use client';  // This tells Next.js to render the component on the client-side

import { useEffect, useState } from 'react';

export default function Checkout() {
  const [product, setProduct] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    const productId = localStorage.getItem("selectedProductId");  // Retrieve product ID from localStorage
    if (productId) {
      const fetchProductData = async () => {
        try {
          const response = await fetch(`http://127.0.0.1:8000/get_products/?id=${productId}`);
          
          // ตรวจสอบว่า response มีสถานะเป็น 200 (OK) หรือไม่
          if (!response.ok) {
            throw new Error('Failed to fetch product details');
          }

          const data = await response.json();  // แปลงข้อมูล response เป็น JSON

          // ตรวจสอบว่าข้อมูลที่รับมามีสินค้าหรือไม่
          if (data.products && data.products.length > 0) {
            setProduct(data.products[0]); // Set the product details
          } else {
            throw new Error('Product not found');
          }
        } catch (error) {
          setError(error.message);  // แสดงข้อผิดพลาด
          console.error('Error fetching product details:', error);
        }
      };
      fetchProductData();
    }
  }, []);

  if (error) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-100">
        <p>{error}</p>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-100">
        <p>Loading product details...</p>
      </div>
    );
  }

  return (
    <div className="h-auto">
      <div className="p-8">
        <h1 className="text-4xl font-bold text-center">Checkout</h1>

        <div className="mt-8 p-4 border rounded-lg">
          <h3 className="text-2xl font-semibold">Product: {product.product_name}</h3>
          <p><strong>Price:</strong> {product.product_price} ฿</p>
          <p><strong>Description:</strong> {product.product_description}</p>
          <p><strong>Rank:</strong> {product.product_rank}</p>

          <div className="text-center mt-8">
            <button className="bg-green-600 text-white px-6 py-2 rounded-md hover:bg-green-500">
              Confirm Donation
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
